.header {
  color: $color-white;
  font-size: 20px;
  line-height: 50px;
  &__name {
    font-family: $font-open-sans-condensed;
    font-size: 16px;
    font-weight: 300;
    text-transform: uppercase; }

  &__phone {
    text-align: center;
    &-main,
    &-cashbox {
      font-size: 16px;
      display: block; } }

  &__ico {
    width: 30px;
    height: 20px;
    margin: 0 10px; }

  &__logo {
    transition: $trans-main;
    width: 100%;
    max-width: 320px;
    margin: -10px 0 -20px 0; }

  &.bg-white {
    height: auto;
    line-height: 100%; }

  a + a {
    margin-left: 0; } }

@media screen and (min-width: 640px) {
  .header__phone {
    &-main,
    &-cashbox {
      display: unset; } } }

@media screen and (min-width: 768px) {
  .header {
    a + a {
      margin-left: 20px; } } }

@media screen and (min-width: 990px) {
  .header {
    height: 50px;
    &__phone {
      text-align: right; } } }
