.footer {
  color: $color-white;
  &__pad {
    padding: 20px 0; }
  ul {
    list-style: none;
    padding: 0 0 0 20%;
    &.social {
      padding: 0;
      a {
        &:hover, &:focus {
          &:after {
            content: none; } } } }

    a {
      display: inline-block;
      text-decoration: none;
      transition: $trans-main;
      position: relative;
      padding: 5px 0;
      &:after {
        content: "";
        position: absolute;
        transition: $trans-main;
        bottom: 0px;
        height: 2px;
        width: 0;
        left: 50%;
        background: transparent; }

      &:hover, &:focus {
        color: $color-white;
        &:after {
          content: "";
          display: inline-block;
          transition: $trans-main;
          text-decoration: underline;
          width: 100%;
          left: 0;
          background: $color-white; } } } }

  h6 {
    margin: 15px 0 15px 20px; }

  .ico {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: 1px solid $color-light-grey;
    fill: $color-light-grey;
    background: $color-dark;
    padding: 15px;
    transition: $trans-main;
    &:hover {
      border: 1px solid $color-white;
      fill: $color-white;
      background: $color-ddark-green;
      padding: 13px; } }

  p {
    font-size: 14px; } }

.social {
  list-style: none;
  padding: 0;
  text-align: center;
  li {
    margin: 5px;
    display: inline-block; } }

.address {
  padding: 0;
  ul {
    padding: 0;
    li {
      line-height: 100%;
      display: flex;
      flex-direction: column; }
    li + li {
      padding-top: 15px; } }

  .one-col,
  .two-col {
    display: inline-block;
    float: left; }
  .one-col {
    width: 40%;
    min-width: 100px;
    font-family: $font-open-sans-condensed;
    font-weight: 700;
    text-transform: uppercase; }
  .two-col {
    width: 60%;
    min-width: 210px;
    a {
      display: block;
      text-decoration: none;
      transition: $trans-main;
      position: relative;
      padding: 5px 0;
      &:after {
        content: "";
        position: absolute;
        transition: $trans-main;
        bottom: 0;
        height: 2px;
        width: 0;
        left: 50%;
        background: transparent; }

      &:hover, &:focus {
        color: $color-white;
        &:after {
          content: "";
          display: inline-block;
          transition: $trans-main;
          text-decoration: underline;
          width: 100%;
          left: 0;
          background: $color-white; } } } } }

.line-gorizontal {
  border-top: 2px solid $color-white; }

@media screen and (min-width: 400px) {
  .address {
    padding: 0 20px 20px 20px; } }

@media screen and (min-width: 640px) {
  .address {
    ul {
      li {
        flex-direction: row; } } }
  .line-vertical + .line-vertical {
    border-left: 2px solid $color-white; }

  .footer__pad {
    padding: 50px 0; } }
