.news {
  margin-bottom: 65px;
  &__big {
    background: $color-grey;
    padding: 35px;
    margin-bottom: 20px;
    &+.news__big {
      margin-top: 30px; }
    &-img {
      img {
        margin: 10px 20px 10px 0;
        max-width: 100%; } }
    &-head, a {
      font-family: $font-open-sans-condensed;
      color: $color-dark-head-news;
      text-transform: uppercase;
      font-size: 32px;
      font-weight: 700;
      text-align: left;
      a {
        display: inline-block;
        text-decoration: none;
        transition: $trans-main;
        position: relative;
        &:after {
          content: "";
          position: absolute;
          transition: $trans-main;
          bottom: 2px;
          height: 2px;
          width: 0;
          left: 50%;
          background: transparent; }

        &:hover, &:focus {
          color: $color-dark;
          &:after {
            content: "";
            display: inline-block;
            transition: $trans-main;
            text-decoration: underline;
            width: 100%;
            left: 0;
            background: $color-ddark-green; } } } }

    &-date {
      font-family: $font-open-sans-condensed;
      font-size: 14px;
      color: $color-red; }
    &-txt {
      color: $color-black;
      font-size: 16px; }

    &-button {
      text-align: right;
      .button {
        display: inline-table;
        a {
          text-align: center;
          font-family: $font-open-sans;
          font-size: 20px;
          font-weight: 400;
          text-transform: none;
          margin-bottom: 0;
          border: 1px solid $color-white;
          @include haf() {
            transition: $trans-main;
            border: 1px solid $color-ddark-green; } } } } }

  &__short {
    background: $color-dark-green;
    height: auto;
    padding: 20px;
    + .news__short {
      margin: 20px 0; }
    &-img {
      height: 210px;
      overflow: hidden;
      margin: 10px 10px 10px -10px;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        height: 100%;
        width: 100%; } }
    &-head {
      font-family: $font-open-sans-condensed;
      color: $color-white;
      font-size: 20px;
      font-weight: 700;
      text-transform: uppercase;
      height: 55px;
      overflow: hidden;
      a {
        text-transform: uppercase;
        font-size: 20px;
        font-family: $font-open-sans-condensed;
        text-decoration: none;
        transition: $trans-main;
        position: relative;
        &:after {
          content: "";
          position: absolute;
          transition: $trans-main;
          bottom: 2px;
          height: 2px;
          width: 0;
          left: 50%;
          background: transparent; }

        &:hover, &:focus {
          color: $color-white;
          &:after {
            content: "";
            display: inline-block;
            transition: $trans-main;
            text-decoration: underline;
            width: 100%;
            left: 0;
            background: $color-white; } } } }
    &-date {
      font-family: $font-open-sans-condensed;
      font-size: 20px;
      color: $color-light-red; }
    &-txt {
      font-size: 16px;
      position: relative;
      line-height: 75%;
      height: 115px;
      overflow: hidden;
      color: $color-light-grey;
      &:after {
        content: "";
        width: 100%;
        height: 40px;
        position: absolute;
        bottom: 0;
        left: 0;
        background: linear-gradient(to bottom, rgba(38,50,56,0) 0%,rgba(38,50,56,1) 70%); } } } }


@media screen and (min-width: 640px) {
  .news {
    &__short {
      &-img {
        float: left;
        width: 210px; } } } }

@media screen and (min-width: 990px) {
  .news {
    &__big {
      margin-bottom: 0; }

    &__short {
      height: 210px;
      padding: 0; } } }
